import { createCrudStore, CrudState } from '../../lib/store-factory';
// REMOVED: import { data as initialData } from './data'; // Mock data no longer used - database is source of truth
import type { Supplier } from './types';
import type { SystemId } from '../../lib/id-types';
import { asSystemId, asBusinessId } from '../../lib/id-types';
import Fuse from 'fuse.js';
import { getCurrentUserSystemId } from '../../contexts/auth-context';

const baseStore = createCrudStore<Supplier>([], 'suppliers', {
  businessIdField: 'id',
  getCurrentUser: getCurrentUserSystemId, // ✅ Track who creates/updates
  apiEndpoint: '/api/suppliers',
});

// ✅ API Sync helpers
const API_ENDPOINT = '/api/suppliers';

const syncToApi = {
  create: async (supplier: Supplier) => {
    try {
      const response = await fetch(API_ENDPOINT, {
        method: 'POST',
        credentials: 'include',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(supplier),
      });
      if (!response.ok) console.warn('[Supplier API] Create sync failed');
      else console.log('[Supplier API] Created:', supplier.systemId);
    } catch (e) {
      console.warn('[Supplier API] Create sync error:', e);
    }
  },
  update: async (systemId: SystemId, updates: Partial<Supplier>) => {
    try {
      const response = await fetch(`${API_ENDPOINT}/${systemId}`, {
        method: 'PATCH',
        credentials: 'include',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(updates),
      });
      if (!response.ok) console.warn('[Supplier API] Update sync failed');
      else console.log('[Supplier API] Updated:', systemId);
    } catch (e) {
      console.warn('[Supplier API] Update sync error:', e);
    }
  },
  delete: async (systemId: SystemId, hard = false) => {
    try {
      const response = await fetch(`${API_ENDPOINT}/${systemId}`, {
        method: 'DELETE',
        credentials: 'include',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ hard }),
      });
      if (!response.ok) console.warn('[Supplier API] Delete sync failed');
      else console.log('[Supplier API] Deleted:', systemId);
    } catch (e) {
      console.warn('[Supplier API] Delete sync error:', e);
    }
  },
  restore: async (systemId: SystemId) => {
    try {
      const response = await fetch(`${API_ENDPOINT}/${systemId}/restore`, {
        method: 'POST',
        credentials: 'include',
        headers: { 'Content-Type': 'application/json' },
      });
      if (!response.ok) console.warn('[Supplier API] Restore sync failed');
      else console.log('[Supplier API] Restored:', systemId);
    } catch (e) {
      console.warn('[Supplier API] Restore sync error:', e);
    }
  },
};

// ✅ Wrap base store methods with API sync
const originalAdd = baseStore.getState().add;
const originalUpdate = baseStore.getState().update;
const originalRemove = baseStore.getState().remove;
const originalHardDelete = baseStore.getState().hardDelete;
const originalRestore = baseStore.getState().restore;

baseStore.setState({
  add: (item: Omit<Supplier, 'systemId'>) => {
    const result = originalAdd(item);
    syncToApi.create(result);
    return result;
  },
  update: (systemId: SystemId, updates: Partial<Supplier>) => {
    originalUpdate(systemId, updates);
    syncToApi.update(systemId, updates);
  },
  remove: (systemId: SystemId) => {
    originalRemove(systemId);
    syncToApi.delete(systemId, false);
  },
  hardDelete: (systemId: SystemId) => {
    originalHardDelete(systemId);
    syncToApi.delete(systemId, true);
  },
  restore: (systemId: SystemId) => {
    originalRestore(systemId);
    syncToApi.restore(systemId);
  },
});

const fuse = new Fuse(baseStore.getState().data, {
    keys: ['name', 'id', 'phone'],
    threshold: 0.3,
});

// Define enhanced interface
interface SupplierStoreState extends CrudState<Supplier> {
  searchSuppliers: (query: string, page: number, limit?: number) => Promise<{ items: { value: string; label: string }[], hasNextPage: boolean }>;
  updateStatus: (systemIds: SystemId[], status: Supplier['status']) => void;
  bulkDelete: (systemIds: SystemId[]) => void;
}

// Augmented methods
const augmentedMethods = {
    searchSuppliers: async (query: string, page: number, limit: number = 20) => {
        return new Promise<{ items: { value: string; label: string }[], hasNextPage: boolean }>(resolve => {
            setTimeout(() => {
                const allSuppliers = baseStore.getState().data;
                const results = query ? fuse.search(query).map(r => r.item) : allSuppliers;
                
                const start = (page - 1) * limit;
                const end = start + limit;
                const paginatedItems = results.slice(start, end);

                resolve({
                    items: paginatedItems.map(s => ({ value: s.systemId, label: s.name })),
                    hasNextPage: end < results.length,
                });
            }, 300);
        });
    },
    updateStatus: (systemIds: SystemId[], status: Supplier['status']) => {
        const currentUser = asSystemId(getCurrentUserSystemId());
        baseStore.setState((state) => ({
            data: state.data.map((item) =>
                systemIds.includes(item.systemId)
                    ? { ...item, status, updatedBy: currentUser, updatedAt: new Date().toISOString() }
                    : item
            ),
        }));
    },
    bulkDelete: (systemIds: SystemId[]) => {
        const currentUser = asSystemId(getCurrentUserSystemId());
        baseStore.setState((state) => ({
            data: state.data.map((item) =>
                systemIds.includes(item.systemId)
                    ? { ...item, isDeleted: true, deletedBy: currentUser, deletedAt: new Date().toISOString() }
                    : item
            ),
        }));
    }
};

// Export typed hook
export const useSupplierStore = (): SupplierStoreState => {
  const state = baseStore();
  return {
    ...state,
    ...augmentedMethods,
  };
};

// Export getState for non-hook usage
useSupplierStore.getState = (): SupplierStoreState => {
  const state = baseStore.getState();
  return {
    ...state,
    ...augmentedMethods,
  };
};
